*{

	box-sizing:border-box;
	margin:0;
	padding:0;
}
.drum-pad {
	font-family: "Open Sans", sans-serif;
	font-size: 16px;
	letter-spacing: 2px;
	text-decoration: none;
	text-transform: uppercase;
	color: #000;
	margin:4px;
	cursor: pointer;
	border: 3px solid;
	padding: 0.25em 0.5em;
	box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
	position: relative;
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;
}
.drum-pad.active {
	transition: all 0.1s ease-in-out;
	box-shadow: 0px 0px 0px 0px;
	top: 5px;
	left: 5px;
}
body{
	width:100vw;
	height:100vh;
	background:rgb(0,0,0);
}
#drum-machine{
	position: absolute;
	top: 50%;
	left: 50%;
	background:white;
	padding:40px;
	transform: translate(-50%, -50%);
}
#display{
	width:200px;
	text-align:center;
	margin:20px 100px;
	font-family: "Open Sans", sans-serif;
	font-size: 14px;
	letter-spacing: 2px;
	text-decoration: none;
	text-transform: uppercase;
	background:rgb(0,0,0);
	color:white;    
	padding:10px;

}
